.menu-container {
    .menu {
        display: flex;
        .link {
            height: 70px;
            border: 1px solid white;
            width: 200px;
            margin: auto;
            border-radius: 20px;
            position: relative;
            list-style-type: none;
            .link_text {
                color: white;
                text-decoration: none;
                margin: 0;
                position: absolute;
                top: 50%;
                left: 50%;
                -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
                margin: auto;
                font-size: 30px
            }
        }
    }
}