.header-container {
    width: 100%;
    padding-bottom: 14px;
    border-bottom: 1px solid rgb(255, 255, 255);
    margin-bottom: 40px;
    display: flex;
    justify-content: center;

    .logout-button {
        height: 32px;
        width: 84px;
        border: none;
        border-radius: 14px;
        margin-top: 32px;
        outline: none;
        margin-left: 20%;
        font-size: 16px;
        background: rgb(226, 226, 226);
        &:hover {
            cursor: pointer;
            background: red;
        }
    }
}